// components/common/Navigation/NavItemComponent.tsx
import React, { useRef, useEffect } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { cn } from '@/lib/utils';
import { NavItemProps } from '@/types/navigation';
import gsap from 'gsap';

export const NavItemComponent: React.FC<NavItemProps> = ({
  item,
  isOpen,
  onMouseEnter,
  onMouseLeave,
}) => {
  const submenuRef = useRef<HTMLDivElement>(null);
  const pathname = usePathname();
  const isActive = pathname === item.href;

  useEffect(() => {
    if (!submenuRef.current) return;

    if (isOpen) {
      gsap.to(submenuRef.current, {
        opacity: 1,
        y: 0,
        duration: 0.3,
        ease: 'power2.out',
        pointerEvents: 'auto',
      });
    } else {
      gsap.to(submenuRef.current, {
        opacity: 0,
        y: -10,
        duration: 0.2,
        ease: 'power2.in',
        pointerEvents: 'none',
      });
    }
  }, [isOpen]);

  return (
    <div
      className="relative"
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
    >
      <Link
        href={item.href}
        className={cn(
          'px-4 py-2 text-sm font-medium transition-colors hover:text-gray-900',
          isActive ? 'text-gray-900' : 'text-gray-600'
        )}
      >
        {item.title}
      </Link>
      {item.submenu && (
        <div
          ref={submenuRef}
          className="absolute left-0 z-50 w-screen bg-white opacity-0 -translate-y-2 top-full"
          style={{ pointerEvents: 'none' }}
        >
          <div className="grid max-w-7xl grid-cols-2 gap-8 px-8 py-6 mx-auto">
            {item.submenu.map((section: { title: string | number | bigint | boolean | React.ReactElement<unknown, string | React.JSXElementConstructor<any>> | Iterable<React.ReactNode> | React.ReactPortal | Promise<string | number | bigint | boolean | React.ReactPortal | React.ReactElement<unknown, string | React.JSXElementConstructor<any>> | Iterable<React.ReactNode> | null | undefined> | Iterable<React.ReactNode> | null | undefined; items: any[]; }, idx: React.Key | null | undefined) => (
              <div key={idx} className="space-y-4">
                <h3 className="text-sm font-medium text-gray-900">
                  {section.title}
                </h3>
                <ul className="space-y-2">
                  {section.items.map((subItem, subIdx) => (
                    <li key={subIdx}>
                      <Link
                        href={subItem.href}
                        className="text-sm text-gray-600 transition-colors hover:text-gray-900"
                      >
                        {subItem.title}
                      </Link>
                    </li>
                  ))}
                </ul>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};