// components/common/Navigation/NavigationMenu.tsx
"use client";


import React, { useState, useRef, useEffect } from 'react';
import { NavItemComponent } from '@/components/common/Navigation/NavItemComponent';
import { navigationItems } from './data';
import gsap from 'gsap';

export const NavigationMenu: React.FC = () => {
  const [openMenu, setOpenMenu] = useState<string | null>(null);
  const navigationRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    gsap.from(navigationRef.current?.children || [], {
      opacity: 0,
      y: -20,
      stagger: 0.1,
      duration: 0.6,
      ease: 'power2.out',
    });
  }, []);

  return (
    <div className="hidden md:flex md:space-x-8" ref={navigationRef}>
      {navigationItems.map((item) => (
        <NavItemComponent
          key={item.title}
          item={item}
          isOpen={openMenu === item.title}
          onMouseEnter={() => setOpenMenu(item.title)}
          onMouseLeave={() => setOpenMenu(null)}
        />
      ))}
    </div>
  );
};